.button {
  min-width: 200px;
  min-height: 42px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 6px;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.19;
  display: block;
  position: relative;
  overflow: hidden;
}

.button:after, .button:before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.button:before {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(90deg, #00c5eb -2%, #b544fe 90%);
  background-repeat: no-repeat;
  background-size: cover;
}

.button:after {
  width: calc(100% - 2px);
  height: calc(100% - 2px);
  opacity: 0;
  background-color: #06071b;
  border-radius: 5px;
  transition: all .3s;
}

.button__text {
  z-index: 10;
  position: relative;
}

.button:hover:after, .button:focus:after {
  opacity: 1;
}

